@use "../../global" as g;

/*!
page > privacy
------------------------------
*/
.privacy01{
    padding: g.vw(45,375) 0 g.vw(25,375);
    @include g.mq('md') {
        padding: g.vw(103) 0 g.vw(30);
    }
    @include g.mq('xxl') {
        padding: 103px 0 30px;
    }
    .c_low_ttl01{
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            margin-bottom: g.vw(43);
        }
        @include g.mq('xxl') {
            margin-bottom: 43px;
        }
    }
    &_desc{
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            margin-bottom: g.vw(43);
        }
        @include g.mq('xxl') {
            margin-bottom: 43px;
        }
    }
    .c_list_number{
        margin-bottom: g.vw(10,375);
        @include g.mq('md') {
            margin-bottom: g.vw(16);
        }
        @include g.mq('xxl') {
            margin-bottom: 16px;
        }
    }
    &_date{
        text-align: right;
        margin-bottom: g.vw(10,375);
        @include g.mq('md') {
            margin-bottom: g.vw(19);
        }
        @include g.mq('xxl') {
            margin-bottom: 19px;
        }
        li{
            color: #9BA6AF;
            display: flex;
            justify-content: flex-end;
        }
        .date{
            display: inline-block;
        }
        .txt_content{
            display: inline-block;
            min-width: g.vw(88,375);
            @include g.mq('md'){
                min-width: g.vw(100);
            }
            @include g.mq('xxl'){
                min-width: 100px;
            }
        }
        .txt{
            display: inline-block;
            margin: 0 g.vw(15,375) 0 0;
            @include g.mq('md'){
                margin: 0 g.vw(15) 0 0;
            }
            @include g.mq('xxl'){
                margin: 0 15px 0 0;
            }
            &:last-child{
                margin: 0;
            }
        }
        &.privacypolicy_date{
            .txt_content{
                display: inline-block;
                min-width: g.vw(100,375);
                @include g.mq('md'){
                    min-width: g.vw(110);
                }
                @include g.mq('xxl'){
                    min-width: 110px;
                }
            }
        }
    }

    &_info{
        text-align: right;
        font-weight: bold;
        font-size: g.vw(18,375);
        line-height: 1.75em;
        @include g.mq('md') {
            line-height: 1.75em;
            font-size: g.vw(20);
        }
        @include g.mq('xxl') {
            font-size: g.rem(20);
        }
    }
}
.privacy02{
    padding: g.vw(25,375) 0;
    @include g.mq('md') {
        padding: g.vw(15) 0;
    }
    @include g.mq('xxl') {
        padding: 15px 0;
    }
    .c_low_ttl01{
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            margin-bottom: g.vw(43);
        }
        @include g.mq('xxl') {
            margin-bottom: 43px;
        }
    }
    &_desc{
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            margin-bottom: g.vw(43);
        }
        @include g.mq('xxl') {
            margin-bottom: 43px;
        }
    }
}
.privacy03{
    padding: g.vw(25,375) 0 0;
    @include g.mq('md') {
        padding: g.vw(27) 0 0;
    }
    @include g.mq('xxl') {
        padding: 27px 0 0;
    }
    .c_low_ttl03{
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            margin-bottom: g.vw(23);
        }
        @include g.mq('xxl') {
            margin-bottom: 23px;
        }
    }
    .privacy_tbl_item{
        margin-bottom: g.vw(35,375);
        @include g.mq('md') {
            margin-bottom: g.vw(43);
        }
        @include g.mq('xxl') {
            margin-bottom: 43px;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }
}

/**/
.alpha{
    list-style-type: lower-alpha;
    margin-top:20px;
    padding-left:0;
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            padding-left: g.vw(33);
            margin-bottom: g.vw(42);
        }
    li{
        margin-bottom:15px;
        }
}
.number{ 
    margin-top:20px;
    padding-left: 0;
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            padding-left: g.vw(33);
            margin-bottom: g.vw(42);
        }
   &_li{
    margin-bottom:15px;
    counter-increment: cnt;
    &::before {
        content: "(" counter(cnt) ") ";
    }
   }
}
.normal_list{
    list-style: inside;
}
.sub{ 
    margin-top:20px;
    padding-left: g.vw(25,375);
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            padding-left: g.vw(33);
            margin-bottom: g.vw(42);
        }
}
.indent_list{
    @include g.mq('md') {
        margin-left:15px;
    }
        @include g.mq('xl') {
            margin-left:2%;
        }
}
.ad_mb{
    margin-bottom:15px;
}
.add_text{
    margin:25px 0 35px;
    text-align: right;
}
.add_wcontents{
    margin:35px 0;
}
.c_list_number{
    &.resize{
        .item{
            &::before {
                top:g.vw(2,375);
                font-size: g.vw(14,375);
                @include g.mq('md') {
                    font-size: g.vw(16);
                    top: g.vw(3);
                }
                @include g.mq('xxl') {
                    font-size: g.rem(16);
                    top:3px;
                }
            }
        }
    }
}